import {UPDATE, BOUGHT} from "./const"
export default {
    initCars(ctx){
        let list = getCar()
        ctx.commit(UPDATE,list)  
        let bought = getCar()
        ctx.commit(BOUGHT,bought)   
        
    },
    addmovies(ctx,good){
        setTimeout(()=>{
            let list = getCar()
          let ishas= list.some(item=>{
                if(item.filmId === good.filmId){
                    item.num++
                    return true
                }
            })
            if(!ishas){
                good.num = 1
                list.push(good)
            }
            // 通知后台更改list
            localStorage.list = JSON.stringify(list)
            ctx.commit(UPDATE,list)            
        },500);
    },
    reducemovies(ctx,good){
        let list = getCar()
          list=list.filter(item=>{
            if(item.filmId === good.filmId){
               
                item.num-- 

                if(item.num <=0 )
                    return false;
 
            }
            return true
        })
        localStorage.list = JSON.stringify(list)
        ctx.commit(UPDATE,list)    
    },
    removemovies(ctx){
        var list = getCar()
        var bought = getBuy()
        bought = list
        list = []
        localStorage.bought = JSON.stringify(bought)
        ctx.commit(BOUGHT,bought)
        localStorage.list = JSON.stringify(list)
        ctx.commit(UPDATE,list)   
    },
    removebought(ctx){
        var bought = getBuy()
        bought = []
        localStorage.bought = JSON.stringify(bought)
        ctx.commit(BOUGHT,bought)
    }
}



// 本地存储进行模拟

// 使用本地存储进行模f
function getCar(){
    return JSON.parse(localStorage.list?localStorage.list:'[]')
}
function getBuy(){
    return JSON.parse(localStorage.bought?localStorage.bought:'[]')
}